.time {
  &__title {
    font-family: var(--Montserrat);
    font-weight: 300;
    font-size: 16px;
    color: inherit;
    text-align: center;
    margin-bottom: 36px;
  }
  &__input {
    width: 115px;
    box-shadow: 3px 3px 0 0 rgba(0, 0, 0, 0.25);
    border-radius: 20px 0 0 20px;
    box-sizing: border-box;
    padding: 10px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    color: #7e7e7e;
    background-color: #d7d7d7;
    border: none;
  }
  &__input-container {
    display: flex;
    align-items: center;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 36px;
    gap: 33px;
  }
  &__result {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    color: inherit;
  }
  &__btn {
    margin-top: 3px;
  }
}
