  inset: 0;
}

.slider:before {
  background-color: #fff;
  border-radius: 100%;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

input:checked + .slider:before {
  content: "";
  background: #000 url("moon.e6f90b43.svg") center / contain no-repeat;
  transform: translateX(20px);
}

